import React, { Component } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import {
  getCurrentSegmentRange,
  getSegmentWidth,
  getStepWidth,
  getVisibleChannels,
} from '../../selectors';
import SegmentCanvas from './SegmentCanvas';

class Plot extends Component {
  render() {
    const {
      data,
      stepWidthOptions,
      currentSegmentRange,
      visibleChannels,
      height,
      segWidth,
    } = this.props;

    const segHeight = height;

    // TODO canvas segments are not connected (gaps in plot), FIXME

    return (
      <div className="plot">
        {currentSegmentRange.map(segNumber => (
          <div
            className="plotSegment"
            key={segNumber}
            style={{ transform: `translateX(${segNumber * segWidth}px)` }}
          >
            <SegmentCanvas
              key={segNumber}
              visibleChannels={visibleChannels}
              stepWidthOptions={stepWidthOptions}
              segment30s={data.onScreenCoordinates[segNumber]}
              segWidth={segWidth}
              height={segHeight}
            />
          </div>
        ))}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  data: state.data,
  stepWidthOptions: getStepWidth(state),
  segWidth: getSegmentWidth(state),
  currentSegmentRange: getCurrentSegmentRange(state),
  visibleChannels: getVisibleChannels(state),
  height: state.presentation.viewportHeight,
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(Plot);
